import React, {useState} from 'react';
import styles from './careers.module.css'
import {Header} from "../../components/header/Header";
import {Footer} from "../../components/footer/Footer";
import {ScrollToTop} from "../../helpers/ScrollToTop";


const vacancies = [
    {
        id: 1,
        title: "-  ",
        location: ", ",
        employment: "  ",
        requirements: [
            "         (, FEED, ).",
            " , , , , , .",
            " -    .",
            "    (Aspen HYSYS, Aspen Plus, SmartPlant P&ID).",
            "   ,    .",
            "       .",
            "   ,    .",
            "    .",
            "   .",
        ],
        responsibilities: [],
        contactEmail: "info.di.intel.design@gmail.com",
    },
    {
        id: 2,
        title: "-  ",
        location: ", ",
        employment: "  ",
        responsibilities: [],
        requirements: [
            "   .",
            "  , .",
            "     , .      .",
            " .      ,  , .",
            "          ;   ,   .",
            "  ,         .",
            "  ()      ,       .",
            "  0,4      .",
            "       .",
            "    0,4-35 .",
            "     6-10-35.",
            "    .",
            "  .",
            "  .",
            "   .",
            "   ."
        ],
        contactEmail: "info.di.intel.design@gmail.com",
    },
    {
        id: 3,
        title: "-   ",
        location: ", ",
        employment: "  ",
        responsibilities: [],
        requirements: [
            "  .",
            "5        ,      ,    .",
            "             3-  Intergraph Smart3D, AVEVA PDMS (E3D), AUTO PLANT 3D.",
            "    .",
            "   .",
            ", ,   ."
        ],
        contactEmail: "info.di.intel.design@gmail.com",
    }
];


export const Careers = () => {

    const [expandedId, setExpandedId] = useState<number | null>(null);

    const toggleDetails = (id: number) => {
        setExpandedId(expandedId === id ? null : id);
    };

    return (
        <div id={'careers'}>
            <ScrollToTop/>
            <Header/>
            <div className={styles.container}>
                <div className={styles.title}>
                    <h1></h1>
                </div>
                <div className={styles.row}>
                    <div className={styles.col5}>
                        {vacancies.map((vacancy, index) => (
                            <div key={index} className={styles.catalogPreview}>
                                <div className={styles.date}>
                                    <span>{vacancy.location}</span>
                                </div>
                                <div className={styles.catalogPreviewContent}>
                                    <h4>{vacancy.title}</h4>
                                </div>
                                <div className={styles.catalogPreviewActions}>
                                    <button onClick={() => toggleDetails(vacancy.id)} className={styles.linkArr}>
                                        {expandedId === vacancy.id ? "" : ""}
                                    </button>
                                </div>
                                {expandedId === vacancy.id && (
                                    <div className={styles.vacancyDetails}>
                                        <p><strong> :</strong> {vacancy.location}</p>
                                        <p><strong>:</strong> {vacancy.employment}</p>
                                        {vacancy.responsibilities.length > 0 && (
                                            <>
                                                <h4>:</h4>
                                                <ul>
                                                    {vacancy.responsibilities.map((resp, index) => (
                                                        <li key={index}>{resp}</li>
                                                    ))}
                                                </ul>
                                            </>
                                        )}
                                        <h4>:</h4>
                                        <ul>
                                            {vacancy.requirements.map((req, index) => (
                                                <li key={index}>{req}</li>
                                            ))}
                                        </ul>

                                        <p>
                                               ,   :{" "}
                                            <a href={`mailto:${vacancy.contactEmail}`}>{vacancy.contactEmail}</a>
                                        </p>
                                    </div>
                                )}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            <Footer/>
        </div>
    );
};
